.head-box {
  //background: linear-gradient(153deg, #342B48 33.79%, #0F4F57 69.42%, $color-default 100%);
  background-image: url(../images/covers/bg_ban.svg);
  background-size: cover;
  min-height: 800px;
  transition: .3s;
}

.head-box-web {
  //background-image: url(../../images/covers/head-web.svg);
}
.head-box-layout {
  padding-top: 92px;
  transition: .3s;
}
.head-box-layout.double-box {
  display: -ms-flexbox;
  display: flex;
}
.head-box-layout .head-box-cover {
  padding-right: 30px;
}
.head-box-layout .head-box-desc {
  padding-top: 150px;
  transition: .3s;
}
.head-box-layout .head-box-desc .title-h1 {
  color: #fff;
  line-height: 100%;
  text-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
}
.head-box-layout .head-box-desc-boxes {
  display: -ms-flexbox;
  display: flex;
  padding-top: 14px;

}
.head-box-layout .head-box-desc-boxes.flex-wrap {
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}
.head-box-layout .head-box-desc-boxes.flex-wrap .head-box-desc-boxes-item {
  flex-basis: 100%; }
.head-box-layout .head-box-desc-boxes-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center; }
.head-box-layout .head-box-desc-boxes-item.icon-fix {
  margin-left: -28px; }
.head-box-layout .head-box-desc-boxes-item span {
  color: $color-default;
  font-size: 18px;
  line-height: 130%;
  font-weight: 300;
}
.head-box-layout .other-desc {
    .title-h1 {
      max-width: 800px;
    }
  .other-desc-content {
    padding-top: 30px;
  }
  .head-box-desc-boxes {
    margin-left: -28px;
    &-item {
      span {
        max-width: 160px;
        margin-left: -18px;
      }
    }
  }
  &-content {
    p {
      font-weight: 500;
      font-size: 18px;
      line-height: 140%;
      color: $color-white;
      max-width: 536px;
    }
  }
}
//Счетчик
.head-box-count {
  display: flex;
  justify-content: space-between;
  max-width: 100%;
  position: relative;
  &-layout {
    position: relative;
  }
  &-item {
    position: absolute;
    .head-box-number {
      font-weight: 900;
      font-size: 220px;
      line-height: 308px;
      transform: matrix(1, 0.57, 0, 0.89, 0, 0);
      color: #fff7f7;
    }
    .head-box-title {
      font-weight: 900;
      font-size: 36px;
      line-height: 43px;
      color: $color-white;
      text-shadow: -15px 8px 30px rgba(0, 0, 0, 0.5);
      transform: matrix(1, -0.57, 0, 0.89, 0, 0);

      position: absolute;
      bottom: 50px;
      right: -171px;
      max-width: 155px;
      transition: .3s;
    }
    &:first-child {
      bottom: -29em;
      left: 19em;
    }
    &:nth-child(2) {
      left: 33em;
      bottom: -21em;
    }
    &:last-child {
      right: 10em;
      top: -7em;
    }
  }
}
//Banner two
.head-box-two {
  background-image: url(../images/covers/home-bg2.png);
  min-height: 616px;
  background-size: cover;
  background-position: bottom;
  transition: .3s;
  &-layout {
    &-content {
      padding-top: 250px;
      max-width: 820px;
      transition: .3s;
      .title-h1 {
        color: $color-white;
      }
    }
  }
  &-desc {
    padding-top: 50px;
    font-weight: bold;
    font-size: 24px;
    line-height: 120%;
    color: $color-white;
    max-width: 796px;
  }
}

@media (max-width: $large-media) {
  .head-box {
    min-height: 736px;
    background-position: bottom;
  }
  .head-box-layout.double-box {
    align-items: center;
  }
  .head-box-layout .head-box-cover {
    img {
      width: 100%;
    }
  }
  .head-box-layout {
    padding-top: 30px;
    .head-box-desc {
      padding-top: 130px;
    }
  }
  .head-box-two-layout-content {
    padding-top: 150px;
  }
}
@media (max-width: $tablet-media) {
  .head-box-layout .head-box-cover {
    display: none;
  }
}
@media (max-width: $mobile-media) {
  .head-box {
    min-height: 546px;
  }
  .head-box-two {
    min-height: 546px;
  }
  .head-box-two-desc {
    font-size: 12px;
    line-height: normal;
  }
  .head-box-layout .head-box-desc {
    padding-top: 90px;
  }
  .head-box-layout .head-box-desc-boxes-item.icon-fix {
    margin-left: -18px;
    margin-bottom: -20px;
  }
  .head-box-layout .head-box-desc-boxes.flex-wrap .head-box-desc-boxes-item {
    img {
      width: 22%;
    }
  }
  .head-box-layout .head-box-desc-boxes {
    flex-wrap: wrap;
  }
  .head-box-layout .head-box-desc-boxes-item {
    flex-basis: 100%;
    margin-bottom: -8px;
    margin-left: 10px;
    img {
      width: 20%;
    }
  }
  .head-box-layout .other-desc-content p {
    font-size: 12px;
    line-height: normal;
  }
  .head-box-layout .other-desc .head-box-desc-boxes-item span {
    margin-left: -12px;
  }
  .head-box-layout .head-box-desc-boxes-item span {
    font-size: 13px;
    line-height: normal;
  }
}
@media (max-width: $mobile-media-small) {

  .head-box-layout .head-box-desc-boxes-item span {
    font-size: 13px;
    line-height: normal;
  }
}